.categories-slider {
  display: flex;
  gap: 16px;
  width: calc(100vw - 2 * 152px);

  &__nav {
    background-color: white;
    width: 32px;
    height: 400px;
    border: 1px solid $middle-gray;
  }

  &__arrow {
    width: 16px;
    height: 16px;
  }

  &__image {
    height: 400px;
    width: calc(100vw * 1440 / 1040);
    // width: 881px;
  }

  &__bars {
    display: flex;
    width: 100%;
    margin-top: 16px;
    gap: 20px;
    justify-content: center;
  }

  &__bar {
    width: 25px;
    height: 5px;
    background-color: $light-gray;

    &--visible {
      background-color: $gray-close-black;
    }
  }
}
